@import url(https://fonts.googleapis.com/css?family=Playfair+Display|Source+Sans+Pro:200,400);
/* Typography imported from Google Fonts */

body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: blanchedalmond;

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
}

p, button{
  font-family: 'Source Sans Pro', sans-serif;
}

/*Generic styles */
html {
  scroll-behavior: smooth;
}

button {
  background-color: #a14444;
  text-decoration: none;
  color: white;
  border-radius: .25rem;
  text-align: center;
  display: inline-block;
  transition: all .3s;
  margin: 10px;
  border-radius: .25rem;
  border-width: .08rem;
}

button:hover {
  opacity: .7;
}

form {
  border-width: 1rem;
 }


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    font-family: 'Playfair Display', serif;
    font-size: 36px;
    color: white;

    background: url('https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8bW92aWUlMjB0aGVhdGVyfGVufDB8fDB8fA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=900&q=60') center ;
    background-size: cover;
    padding: 1rem 3rem;
    min-height: 10vh;
    max-height: 65vh;

    /* grid styles */
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    -webkit-align-items: center;
            align-items: center;

}

.header > h1 {
    font-size: 4rem;
    font-weight: 300;
    padding: 1rem;
}

.header > div > p {
    font-size: 1.5rem;
    font-weight: 600;
}
.footer {
    background-color: #160708;
    min-height: 15vh;
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column; 
    -webkit-align-items: center; 
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 1vmin);
    color: white;
    bottom: 0;
    left:0;
    width: 100%;
}
  .moviesearchlist {
    width: 95%;
    margin: 1rem auto; 
    padding: 20px; 
    border: 1px dotted black; 
    min-height: 75vh; 
    max-height: 80vh;
    overflow-y: auto;
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column;
    
}

.searchform {
    background-color: #dddbdb;
    background-size: cover;
    padding: .5rem;

    display: grid;
    -webkit-align-items: center;
            align-items: center;
    grid-template-columns: repeat (auto-fit, minmax(240px, 1fr));
    margin-bottom: 1rem;
}

form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.search {
    background-color: #628565;
    padding: 1rem .7rem;
    height: 10px;
    color: white; 
    border-radius: 12px;
    line-height: 0px;
    font-size: 15px;
    font-weight: bold;
    margin: 0;
}

label {
    font-size: 1rem;
    font-weight: bold;
}


#queryInput {
    box-shadow: 0 0 15px 4px rgba(0,0,0,0.06);
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    height: 2.5rem;
}

.searchResults {
    margin-top: 3rem;
}
.movieinfocard {
    padding-top: 20px;
}


td, th {
    border: 1px solid #160708;
    padding: 0.5rem;
}


.card {
    border: 1px solid #160708;
    border-radius: .5rem;
    
}

.card > div> img {
    max-height: 50%;
    overflow: hidden;
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
    position: relative;
    max-width: 100%;
}

.card-title{
    font-family: 'Playfair Display', serif;
    font-size: 1.25rem;
}

  
  
